  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-transition: visibility 0.38s, -webkit-transform ease-in-out 0.38s;
  transition: visibility 0.38s, -webkit-transform ease-in-out 0.38s;
  transition: transform ease-in-out 0.38s, visibility 0.38s;
  transition: transform ease-in-out 0.38s, visibility 0.38s, -webkit-transform ease-in-out 0.38s;
}
.vux-drawer > .vux-drawer-body > .drawer-mask {
  z-index: 9999;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity ease-in-out 0.38s, visibility ease-in-out 0.38s;
  transition: opacity ease-in-out 0.38s, visibility ease-in-out 0.38s;
  background-color: rgba(0, 0, 0, 0.3);
}
.vux-drawer > .vux-drawer-body > .vux-drawer-active {
  visibility: visible;
  opacity: 1;
}
.vux-drawer > .vux-drawer-content {
  background-color: #fff;
  position: absolute;
  top: 0;
  height:100%;
  overflow: hidden;
  pointer-events: none;
  visibility: hidden;
  -webkit-transition: visibility 0.38s, -webkit-transform ease-in-out 0.38s;
  transition: visibility 0.38s, -webkit-transform ease-in-out 0.38s;
  transition: transform ease-in-out 0.38s, visibility 0.38s;
  transition: transform ease-in-out 0.38s, visibility 0.38s, -webkit-transform ease-in-out 0.38s;
  will-change: none;
}
.vux-drawer > .drawer-left {
  left: 0;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.vux-drawer > .drawer-right {
  right: 0;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.vux-drawer > .vux-drawer-active {
  pointer-events: inherit;
  visibility: visible;
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}
